/*
* Stylesheet for inputs
* @author: Dory A.Azar
*/

input {
    max-width: 31.25rem;
    background-color: transparent;
    border: none;
    margin: 0 1rem;
    padding: 0;
    padding-right: 1.25rem;
    color: rgba(0, 0, 0, .87);
    word-wrap: break-word;
    outline: none;
    display: flex;
    flex: 100% 1;
    -webkit-tap-highlight-color: transparent;
    height: 2.75rem;
    font-size: 1rem;
}

.inputContainer {
    background: #fff;
    display: flex;
    border: 1px solid #dfe1e5;
    box-shadow: none;
    border-radius: 1.5rem;
    z-index: 3;
    height: 2.75rem;
    margin: 1rem auto;
    width: auto;
    max-width: 36.25rem;
}

.inputContainer:hover  {
    background-color: #fff;
    box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
    border-color: rgba(223, 225, 229, 0);
}
/*
* Stylesheet for form
* @author: Dory A.Azar
*/

.instruction-text {
    font-size: 0.9rem;
}
/*
* Stylesheet for radio buttons
* @author: Dory A.Azar
*/

.radio-button {
    border: 1px solid #3c4043;
    border-right:none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 0;
    cursor: pointer;
  }
  
  .radio-button:last-child {
    border-right: 1px solid #3c4043;
  }
  
  .radio-button:active, .radio-button:hover {
    background-color: #d0d0d0;
  }

.selected {
    padding: 15px 32px;
    background-color: #000;
    border: 1px solid #000;
    color: #fff;
  }
/*
* Stylesheet for react-elements
* @author: Dory A.Azar
*/

body * {
    font-family: Roboto, arial, sans-serif;
    font-size: 16px;
}

.App-component {
  border: 1px solid;
  margin: 10px;
  margin-bottom: 50px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 26px;
}

.App-content {
  display: flex;
  justify-content: center;
  padding: 20px;
}

